@import url('https://fonts.googleapis.com/css2?family=Marcellus&display=swap');
$pink: #FFD9D2;

* {
    margin: 0;
    padding: 0;
    line-height: 1.4;
    box-sizing: border-box;
    font-family: 'Marcellus', serif !important;
}
ul {
    padding: 0;
    list-style: none;
}
a {
    text-decoration: none;
    color: #333;
}
img {
    width: 100%;
}

.hide {
    display: none;
}
.topHeader {
    padding-block: .5rem;
    background-color: $pink;
    color: #FFF;
    text-shadow: 1px 1px 2px #000;
    display: grid;
    grid-template-columns: 1fr auto;
    padding-inline: 2em;
    & a {
        color: #FFF;
        text-decoration: none;
        &:hover {
            color: #333;
            text-decoration: none;
        }
    }
    & ul {
        display: grid;
        grid-gap: calc(100vw * .01);
        grid-template-columns: repeat(7, auto);
    }
}
.topNavbar {
    position: relative;
    & .nav-main-menu {
        position: absolute;
        width: 100%;
        z-index: 2;
        display: grid;
        padding: 0 2em;
        grid-template-columns: 1fr auto auto;
        & .logo {
            height: calc(100vw * .1);
            max-height: 100px;
        }
        &:hover {
            background-color: #FFF;
            opacity: .7;
            & li:not(:nth-child(n+7)):after {
                content: '';
                border-bottom: solid 1px #000;  
                transform: scaleX(0);  
                transition: transform 250ms ease-in-out;
            }
            & li:hover:after { 
                transform: scaleX(1); 
                transform-origin:  0% 100%;
            }

            & a {
                color: #333;
                text-shadow: none;
                font-weight: 900;
                &:hover {
                    color: $pink;
                    text-shadow: 1px 1px 1px #333;
                }
            }
        }
        & ul {
            display: grid;
            grid-column-gap: calc(100vw * .03);
            grid-template-columns: repeat(8, auto);
            & li {
                display: grid;
                font-size: 1em;
                & a {
                    align-self: center;
                    text-decoration: none;
                    color: #FFF;
                    text-shadow: 1px 1px 2px #000;
                }
            }
        }
        & .hamburger {
            display: none;
        }
    }
    & .hamburger {
        align-self: center;
        cursor: pointer;
    }
    & .hamburger > span {
        z-index: 3;
        display: block;
        width: 25px;
        height: 3px;
        margin: 2px auto;
        transition: all 0.3s ease-in-out;
        background-color: grey;
    }
    & .nav-side-menu {
        z-index: 22;
        position: fixed;
        top: 0;
        right: 0;
        width: 400px;
        height: 100vh;
        background: #FFF !important;
        & .icon-close {
            padding: 20px;
            position: absolute;
            top: 10px;
            right: 15px;
            color: #000;
            cursor: pointer;
        }
    }
    & .nav-side-menu-content {
        text-align: center;
        & img {
            margin-left: 15%;
        }
        & ul {
            list-style: none;
            font-size: 24px;
            & a {
                color: #000;
                text-decoration: none;
            }
        }
    }
}
.carousel {
    height: calc(100vw * .515);
    & .carousel-item {
        height: calc(100vw * .515);
        background: no-repeat center top scroll;
        background-size: contain;
    }
    & .carousel-caption {
        padding-block: 15%;
        & h3 {
            color: #000;
            font-size: calc(100vw * .06);
        }
    }
    & button {
        padding: 0.5em 1em;
        border: none;
        color: #FFF;
        background-color: #000;
        justify-self: center;
        & a {
          text-decoration: none;
          color: #FFF;
        }
    }
}

.footer {
    padding: 2em 2em;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(auto, 200px));
    justify-content: center;
    align-items: start;
    grid-gap: 1em;
    border-top: 1px solid #e9e9e9;
}
.copyright {
    text-align: center;
    padding-block: 1.3em;
    background-color: $pink;
    opacity: .6;
    font-size: 0.75em;
}
.overlay {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    background:rgba(0,0,0,0.5);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.subscribe-modal {
    position: fixed;
    top: calc(50% - 150px);
    left: calc(50% - 250px);
    z-index: 25;
    background-color: #000;
    color: #FFF;
    max-width: 500px;
    min-height: 300px;
    max-height: 75vh;
    border-radius: 5px;
    padding: 3em 4em;
    .icon-close {
        position: absolute;
        padding: 10px;
        top: 10px;
        right: 15px;
        color: #FFF;
        cursor: pointer;
    }
    & form {
        width: 100%;
        display: grid;
        grid-template-rows: repeat(5, auto);
        grid-gap: 1em;
        justify-items: stretch;
        text-align: center;
        & :first-child {
            font-size: 30px;
        }
        & :last-child {
            font-size: 10px;
            opacity: 0.5;
        }
    }
}
.wrapper {
    position: relative;
    margin-block: 6rem 1rem;
    max-width: 60rem;
    margin-inline: auto;
    text-align: center;
}
.title {
    background-color: $pink;
    padding: 0 5rem;
}

@media (max-width: 767px) {
    .topHeader {
        font-size: 3.5vw;
        & ul {
            display: none;
        }
    }
    .topNavbar .nav-main-menu {
        & ul {
            grid-gap: 0;
        }
        & li:not(:nth-child(n+6)) {
            display: none;
        }
        & .hamburger {
            display: block;
        }
    }
}