@import url(https://fonts.googleapis.com/css2?family=Marcellus&display=swap);
.collection-items{display:grid;grid-gap:1.5rem;grid-template-columns:repeat(auto-fit, minmax(220px, 1fr));padding:2rem 1rem}.wrapper button{grid-column:span 3;padding:.5em 1em;border:none;color:#fff;background-color:#000}.wrapper button a{color:#fff}.products{display:grid;grid-gap:1rem;grid-template-columns:repeat(auto-fit, minmax(270px, 1fr));padding:2rem 1rem}.about{padding:2rem 1rem}.faq{padding:2rem 1rem}.reviews{display:grid;grid-auto-flow:row dense;-webkit-align-items:start;align-items:start;grid-gap:1rem;grid-template-columns:repeat(auto-fit, minmax(220px, 1fr));padding:2rem 1rem}.reviews .card-content{text-align:left;padding-inline:1em}.reviews li:nth-child(2){font-weight:900}.reviews li:nth-child(3){font-size:10px}
*{margin:0;padding:0;line-height:1.4;box-sizing:border-box;font-family:"Marcellus",serif !important}ul{padding:0;list-style:none}a{text-decoration:none;color:#333}img{width:100%}.hide{display:none}.topHeader{padding-block:.5rem;background-color:#ffd9d2;color:#fff;text-shadow:1px 1px 2px #000;display:grid;grid-template-columns:1fr auto;padding-inline:2em}.topHeader a{color:#fff;text-decoration:none}.topHeader a:hover{color:#333;text-decoration:none}.topHeader ul{display:grid;grid-gap:calc(100vw * .01);grid-template-columns:repeat(7, auto)}.topNavbar{position:relative}.topNavbar .nav-main-menu{position:absolute;width:100%;z-index:2;display:grid;padding:0 2em;grid-template-columns:1fr auto auto}.topNavbar .nav-main-menu .logo{height:calc(100vw * .1);max-height:100px}.topNavbar .nav-main-menu:hover{background-color:#fff;opacity:.7}.topNavbar .nav-main-menu:hover li:not(:nth-child(n+7)):after{content:"";border-bottom:solid 1px #000;-webkit-transform:scaleX(0);transform:scaleX(0);transition:-webkit-transform 250ms ease-in-out;transition:transform 250ms ease-in-out;transition:transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out}.topNavbar .nav-main-menu:hover li:hover:after{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:0% 100%;transform-origin:0% 100%}.topNavbar .nav-main-menu:hover a{color:#333;text-shadow:none;font-weight:900}.topNavbar .nav-main-menu:hover a:hover{color:#ffd9d2;text-shadow:1px 1px 1px #333}.topNavbar .nav-main-menu ul{display:grid;grid-column-gap:calc(100vw * .03);grid-template-columns:repeat(8, auto)}.topNavbar .nav-main-menu ul li{display:grid;font-size:1em}.topNavbar .nav-main-menu ul li a{-webkit-align-self:center;align-self:center;text-decoration:none;color:#fff;text-shadow:1px 1px 2px #000}.topNavbar .nav-main-menu .hamburger{display:none}.topNavbar .hamburger{-webkit-align-self:center;align-self:center;cursor:pointer}.topNavbar .hamburger>span{z-index:3;display:block;width:25px;height:3px;margin:2px auto;transition:all .3s ease-in-out;background-color:gray}.topNavbar .nav-side-menu{z-index:22;position:fixed;top:0;right:0;width:400px;height:100vh;background:#fff !important}.topNavbar .nav-side-menu .icon-close{padding:20px;position:absolute;top:10px;right:15px;color:#000;cursor:pointer}.topNavbar .nav-side-menu-content{text-align:center}.topNavbar .nav-side-menu-content img{margin-left:15%}.topNavbar .nav-side-menu-content ul{list-style:none;font-size:24px}.topNavbar .nav-side-menu-content ul a{color:#000;text-decoration:none}.carousel{height:calc(100vw * .515)}.carousel .carousel-item{height:calc(100vw * .515);background:no-repeat center top scroll;background-size:contain}.carousel .carousel-caption{padding-block:15%}.carousel .carousel-caption h3{color:#000;font-size:calc(100vw * .06)}.carousel button{padding:.5em 1em;border:none;color:#fff;background-color:#000;justify-self:center}.carousel button a{text-decoration:none;color:#fff}.footer{padding:2em 2em;display:grid;grid-template-columns:repeat(auto-fit, minmax(auto, 200px));-webkit-justify-content:center;justify-content:center;-webkit-align-items:start;align-items:start;grid-gap:1em;border-top:1px solid #e9e9e9}.copyright{text-align:center;padding-block:1.3em;background-color:#ffd9d2;opacity:.6;font-size:.75em}.overlay{position:fixed;z-index:20;top:0;left:0;background:rgba(0,0,0,.5);width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.subscribe-modal{position:fixed;top:calc(50% - 150px);left:calc(50% - 250px);z-index:25;background-color:#000;color:#fff;max-width:500px;min-height:300px;max-height:75vh;border-radius:5px;padding:3em 4em}.subscribe-modal .icon-close{position:absolute;padding:10px;top:10px;right:15px;color:#fff;cursor:pointer}.subscribe-modal form{width:100%;display:grid;grid-template-rows:repeat(5, auto);grid-gap:1em;justify-items:stretch;text-align:center}.subscribe-modal form :first-child{font-size:30px}.subscribe-modal form :last-child{font-size:10px;opacity:.5}.wrapper{position:relative;margin-block:6rem 1rem;max-width:60rem;margin-inline:auto;text-align:center}.title{background-color:#ffd9d2;padding:0 5rem}@media(max-width: 767px){.topHeader{font-size:3.5vw}.topHeader ul{display:none}.topNavbar .nav-main-menu ul{grid-gap:0}.topNavbar .nav-main-menu li:not(:nth-child(n+6)){display:none}.topNavbar .nav-main-menu .hamburger{display:block}}
