
.collection-items {
	display: grid;
	grid-gap: 1.5rem;
	grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
	padding: 2rem 1rem;
}
.wrapper button {
    grid-column: span 3;
    padding: 0.5em 1em;
    border: none; 
    color: #FFF;
    background-color: #000;
    & a {
      color: #FFF;
    }
}
.products {
	display: grid;
	grid-gap: 1rem;
	grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
	padding: 2rem 1rem;
}
.about {
  	padding: 2rem 1rem;
}
.faq {
 	padding: 2rem 1rem;
}
.reviews {
	display: grid;
	grid-auto-flow: row dense;
	align-items: start;
	grid-gap: 1rem;
	grid-template-columns: repeat(auto-fit, minmax(220px,1fr));
	padding: 2rem 1rem;
	& .card-content {
		text-align: left;
		padding-inline: 1em;
	}
	li {
		&:nth-child(2) {
		font-weight: 900;
		}
		&:nth-child(3) {
		font-size: 10px;
		}
	}
}

